<template>
  <div class="hs-container">
    <c-header leftIcon1="home_2.png" :leftFn1="_gotoHomePage" :title="$t('base.DomesticFlightsOrderConfirmation')"></c-header>
    <hs-scroll class="table-view">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="state-details">
              <div class="header">
                <p>{{Number(flightOrder.TravelType) === 0 ? $t('base.BusinessTrip') : $t('base.PersonalTrip')}}</p>
              </div>
              <p class="infor">{{$t('base.OrderNumber')}}: {{flightOrder.TravelID}}</p>
              <p class="infor">{{$t('base.OrderDate')}}: {{$t(beTime(new Date()), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(new Date(), 'hh:mm')}}</p>
              <!-- <div class="buttons" v-if="!submitSuccess">
                <hs-button class="button" v-if="Number(flightOrder.PayType) === 2 && noValue(JSON.stringify(flightOrder.VettingProcessList))" @click="_gotoSubmitAndPay">{{$t('base.SubmitAndPay')}}</hs-button>
                <hs-button class="button" v-else @click="_confirm">{{$t('base.Submit')}}</hs-button>
              </div>
              <div class="buttons" v-else-if="submitSuccess && Number(flightOrder.PayType) === 2 && noValue(JSON.stringify(flightOrder.VettingProcessList))">
                <hs-button class="button" @click="_gotoPay">{{$t('base.Pay')}}</hs-button>
              </div> -->
              <div class="buttons">
                <hs-button v-if="flightOrder.PageBtnInfo.IsAllowPayAndSubmit" @click="_gotoSubmitAndPay"
                  class="button">{{$t('base.SubmitAndPay')}}</hs-button>
                <hs-button v-if="flightOrder.PageBtnInfo.IsAllowSubmit" @click="_confirm"
                  class="button">{{$t('base.Submit')}}</hs-button>
                <hs-button v-if="flightOrder.PageBtnInfo.IsAllowPay" @click="_gotoPay"
                  class="button">{{$t('base.Pay')}}</hs-button>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 去程 -->
        <hs-section v-if="!noValue(flightOrder.GoSelectedFlight.FlightNo)">
          <hs-cell-1 :showLogo="false">
            <div class="travel-details">
              <div class="time">
                <div class="mark" :type="language">{{noValue(flightOrder.BackSelectedFlight.FlightNo) ? $t('base.OneWay') : $t('base.Going')}}</div>
                <p>{{$t(flightOrder.GoSelectedFlight.DepartDate, 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(flightOrder.GoSelectedFlight.DepartDate)}} {{flightOrder.DepartObject[`AirPortName_${language}`]}}&nbsp;-&nbsp;{{flightOrder.ArrivalObject[`AirPortName_${language}`]}}</p>
              </div>
              <div class="plane">
                <div class="logo" :style="{'background-image': `url(${flightOrder.GoSelectedFlight.FlightDetails.AirLineLogoUrl})`}"></div>
                <div class="details">
                  <p>
                    {{flightOrder.GoSelectedFlight.FlightDetails.AirLineName}}
                    <span v-if="flightOrder.GoSelectedFlight.FlightDetails.IsCodeShare">{{$t('base.CodeShare')}}</span>
                    {{flightOrder.GoSelectedFlight.FlightDetails.FlightNo}}
                    <template v-if="flightOrder.GoSelectedFlight.FlightDetails.PlaneName">|&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.PlaneName}}</template>
                    |&nbsp;{{flightOrder.GoSelectedFlight.FlightDetails.HasMeal ? $t('base.Meals') : $t('base.NoMeal')}}
                    <template v-if="flightOrder.GoSelectedFlight.CabinDetails.CabinName">|&nbsp;{{flightOrder.GoSelectedFlight.CabinDetails.CabinName}}</template>
                  </p>
                  <p v-if="flightOrder.GoSelectedFlight.FlightDetails.IsCodeShare">
                    {{$t('base.OperatedBy')}}
                    {{flightOrder.GoSelectedFlight.FlightDetails.ActualCarryAirLineName}}
                    {{flightOrder.GoSelectedFlight.FlightDetails.ActualCarryFlightNo}}
                  </p>
                </div>
              </div>
              <div class="details">
                <div class="source">
                  <p>{{beTime(flightOrder.GoSelectedFlight.FlightDetails.DepartDateTime, 'hh:mm')}}</p>
                  <p>{{flightOrder.GoSelectedFlight.FlightDetails.DepartAirportName}} {{flightOrder.GoSelectedFlight.FlightDetails.DepartTerminal}}</p>
                </div>
                <div class="to">
                  <div class="stopOver-logo" v-if="flightOrder.GoSelectedFlight.FlightDetails.StopOver">
                    <p>{{$t('base.By')}}</p>
                  </div>
                  <div class="logo" v-else></div>
                  <div class="dashed"></div>
                  <p class="stopOver" v-if="flightOrder.GoSelectedFlight.FlightDetails.StopOver">{{flightOrder.GoSelectedFlight.FlightDetails.StopOver.StopCity}}</p>
                </div>
                <div class="destination">
                  <p>
                    {{beTime(flightOrder.GoSelectedFlight.FlightDetails.ArrivalDateTime, 'hh:mm')}}
                    <span v-if="flightOrder.GoSelectedFlight.FlightDetails.AddDays > 0">
                      <template v-if="language === 'CN'">+{{flightOrder.GoSelectedFlight.FlightDetails.AddDays}}天</template>
                      <template v-else>+{{flightOrder.GoSelectedFlight.FlightDetails.AddDays}}</template>
                    </span>
                  </p>
                  <p>{{flightOrder.GoSelectedFlight.FlightDetails.ArrivalAirportName}} {{flightOrder.GoSelectedFlight.FlightDetails.ArrivalTerminal}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" :showSeparator="!flightOrder.GoLowestPriceFlights[0].openLowestPrice" v-if="flightOrder.GoLowestPriceFlights && flightOrder.GoLowestPriceFlights[0]" @click.native="_toggleOpenLowestPrice('GoLowestPriceFlights')">
            <div class="min-price-details-header">
              <p>{{$t('base.LowestPriceDetails')}}</p>
              <div style="background-image: url('static/image/base/cell/right_arrow_gray.png')" :style="{transform: `rotate(${flightOrder.GoLowestPriceFlights[0].openLowestPrice ? '90deg' : '-90deg'})`}"></div>
            </div>
          </hs-cell-1>
          <div class="min-price-details" v-if="flightOrder.GoLowestPriceFlights && flightOrder.GoLowestPriceFlights[0] && flightOrder.GoLowestPriceFlights[0].openLowestPrice">
            <div>
              <p>{{$t('base.XHoursBeforeAndAfterTheLowestPrice', 'function', flightOrder.GoLowestPriceFlights[0].FloorPriceTimeRange)}}</p>
              <p class="price">￥{{flightOrder.GoLowestPriceFlights[0].CabinDetails.SalePrice}}</p>
            </div>
            <div>
              <p>{{$t('base.CheapestFlight')}}</p>
              <p>{{flightOrder.GoLowestPriceFlights[0].FlightNo}}</p>
            </div>
            <div>
              <p>{{$t('base.ReasonForNotSelectingTheCheapestFlight')}}</p>
              <p>{{flightOrder.GoLowestPriceFlights[0].ViolationFloorPirceReason}}</p>
            </div>
          </div>
          <hs-cell-1 :showLogo="false" @click.native="_getFlightCabinRule(flightOrder.GoSelectedFlight.FlightDetails, flightOrder.GoSelectedFlight.CabinDetails)">
            <div class="cost-details" :type="language">
              <div class="details">
                <div><span>{{$t('base.AirTickets')}}</span><span>￥{{flightOrderInit.GoChargeInfo && flightOrderInit.GoChargeInfo.SalePrice ? flightOrderInit.GoChargeInfo.SalePrice : 0}}</span></div>
                <div>
                  <span>{{$t('base.PlaneConstruction')}}&nbsp;/&nbsp;{{$t('base.Fuel')}}</span>
                  <span>
                    ￥{{
                      (flightOrderInit.GoChargeInfo && flightOrderInit.GoChargeInfo.FuelOilTax ? flightOrderInit.GoChargeInfo.FuelOilTax : 0) +
                      (flightOrderInit.GoChargeInfo && flightOrderInit.GoChargeInfo.AirportTax ? flightOrderInit.GoChargeInfo.AirportTax : 0)
                    }}
                  </span>
                </div>
                <div v-if="flightOrderInit.GoChargeInfo && flightOrderInit.GoChargeInfo.Rebate > 0">
                  <span>{{$t('base.FrontReturn')}}</span>
                  <span>-￥{{flightOrderInit.GoChargeInfo.Rebate}}</span>
                </div>
              </div>
              <p>{{$t('base.TicketRestrictions')}} ></p>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 回程 -->
        <hs-section v-if="!noValue(flightOrder.BackSelectedFlight.FlightNo)">
          <hs-cell-1 :showLogo="false">
            <div class="travel-details">
              <div class="time">
                <div class="mark" :type="language">{{$t('base.Return')}}</div>
                <p>{{$t(flightOrder.BackSelectedFlight.DepartDate, 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(flightOrder.BackSelectedFlight.DepartDate)}} {{flightOrder.ArrivalObject[`AirPortName_${language}`]}}&nbsp;-&nbsp;{{flightOrder.DepartObject[`AirPortName_${language}`]}}</p>
              </div>
              <div class="plane">
                <div class="logo" :style="{'background-image': `url(${flightOrder.BackSelectedFlight.FlightDetails.AirLineLogoUrl})`}"></div>
                <div class="details">
                  <p>
                    {{flightOrder.BackSelectedFlight.FlightDetails.AirLineName}}
                    <span v-if="flightOrder.BackSelectedFlight.FlightDetails.IsCodeShare">{{$t('base.CodeShare')}}</span>
                    {{flightOrder.BackSelectedFlight.FlightDetails.FlightNo}}
                    <template v-if="flightOrder.BackSelectedFlight.FlightDetails.PlaneName">|&nbsp;{{flightOrder.BackSelectedFlight.FlightDetails.PlaneName}}</template>
                    |&nbsp;{{flightOrder.BackSelectedFlight.FlightDetails.HasMeal ? $t('base.Meals') : $t('base.NoMeal')}}
                    <template v-if="flightOrder.BackSelectedFlight.CabinDetails.CabinName">|&nbsp;{{flightOrder.BackSelectedFlight.CabinDetails.CabinName}}</template>
                  </p>
                  <p v-if="flightOrder.BackSelectedFlight.FlightDetails.IsCodeShare">
                    {{$t('base.OperatedBy')}}
                    {{flightOrder.BackSelectedFlight.FlightDetails.ActualCarryAirLineName}}
                    {{flightOrder.BackSelectedFlight.FlightDetails.ActualCarryFlightNo}}
                  </p>
                </div>
              </div>
              <div class="details">
                <div class="source">
                  <p>{{beTime(flightOrder.BackSelectedFlight.FlightDetails.DepartDateTime, 'hh:mm')}}</p>
                  <p>{{flightOrder.BackSelectedFlight.FlightDetails.DepartAirportName}} {{flightOrder.BackSelectedFlight.FlightDetails.DepartTerminal}}</p>
                </div>
                <div class="to">
                  <div class="stopOver-logo" v-if="flightOrder.BackSelectedFlight.FlightDetails.StopOver">
                    <p>{{$t('base.By')}}</p>
                  </div>
                  <div class="logo" v-else></div>
                  <div class="dashed"></div>
                  <p class="stopOver" v-if="flightOrder.BackSelectedFlight.FlightDetails.StopOver">{{flightOrder.BackSelectedFlight.FlightDetails.StopOver.StopCity}}</p>
                </div>
                <div class="destination">
                  <p>{{beTime(flightOrder.BackSelectedFlight.FlightDetails.ArrivalDateTime, 'hh:mm')}}<span v-if="flightOrder.BackSelectedFlight.FlightDetails.AddDays > 0">+{{flightOrder.BackSelectedFlight.FlightDetails.AddDays}}</span></p>
                  <p>{{flightOrder.BackSelectedFlight.FlightDetails.ArrivalAirportName}} {{flightOrder.BackSelectedFlight.FlightDetails.ArrivalTerminal}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" :showSeparator="!flightOrder.BackLowestPriceFlights[0].openLowestPrice" v-if="flightOrder.BackLowestPriceFlights && flightOrder.BackLowestPriceFlights[0]" @click.native="_toggleOpenLowestPrice('BackLowestPriceFlights')">
            <div class="min-price-details-header">
              <p>{{$t('base.LowestPriceDetails')}}</p>
              <div style="background-image: url('static/image/base/cell/right_arrow_gray.png')" :style="{transform: `rotate(${flightOrder.BackLowestPriceFlights[0].openLowestPrice ? '90deg' : '-90deg'})`}"></div>
            </div>
          </hs-cell-1>
          <div class="min-price-details" v-if="flightOrder.BackLowestPriceFlights && flightOrder.BackLowestPriceFlights[0] && flightOrder.BackLowestPriceFlights[0].openLowestPrice">
            <div>
              <p>{{$t('base.XHoursBeforeAndAfterTheLowestPrice', 'function', flightOrder.BackLowestPriceFlights[0].FloorPriceTimeRange)}}</p>
              <p class="price">￥{{flightOrder.BackLowestPriceFlights[0].CabinDetails.SalePrice}}</p>
            </div>
            <div>
              <p>{{$t('base.CheapestFlight')}}</p>
              <p>{{flightOrder.BackLowestPriceFlights[0].FlightNo}}</p>
            </div>
            <div>
              <p>{{$t('base.ReasonForNotSelectingTheCheapestFlight')}}</p>
              <p>{{flightOrder.BackLowestPriceFlights[0].ViolationFloorPirceReason}}</p>
            </div>
          </div>
          <hs-cell-1 :showLogo="false" @click.native="_getFlightCabinRule(flightOrder.BackSelectedFlight.FlightDetails, flightOrder.BackSelectedFlight.CabinDetails)">
            <div class="cost-details" :type="language">
              <div class="details">
                <div><span>{{$t('base.AirTickets')}}</span><span>￥{{flightOrderInit.BackChargeInfo && flightOrderInit.BackChargeInfo.SalePrice ? flightOrderInit.BackChargeInfo.SalePrice : 0}}</span></div>
                <div>
                  <span>{{$t('base.PlaneConstruction')}}&nbsp;/&nbsp;{{$t('base.Fuel')}}</span>
                  <span>
                    ￥{{
                      (flightOrderInit.BackChargeInfo && flightOrderInit.BackChargeInfo.FuelOilTax ? flightOrderInit.BackChargeInfo.FuelOilTax : 0) +
                      (flightOrderInit.BackChargeInfo && flightOrderInit.BackChargeInfo.AirportTax ? flightOrderInit.BackChargeInfo.AirportTax : 0)
                    }}
                  </span>
                </div>
                <div v-if="flightOrderInit.BackChargeInfo && flightOrderInit.BackChargeInfo.Rebate > 0">
                  <span>{{$t('base.FrontReturn')}}</span>
                  <span>-￥{{flightOrderInit.BackChargeInfo.Rebate}}</span>
                </div>
              </div>
              <p>{{$t('base.TicketRestrictions')}} ></p>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 乘客信息 -->
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in flightOrder.Passengers" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.Passenger')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === flightOrder.Passengers.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{item.Credential.CredentialName}} {{item.Credential.CredentialNo}}</p>
                  <p class="insure" v-for="(itemChild, indexChild) in item.Insurances" :key="indexChild">
                    {{itemChild.InsuranceCategory}}
                    <template v-if="item.Order && !noValue(flightOrder.BackSelectedFlight.FlightNo)">({{itemChild.Order === 1 ? $t('base.Going') : $t('base.Return')}})</template>
                    x 1{{$t('base.Copy')}}
                  </p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 其他信息 -->
        <hs-section v-if="flightOrderInit.IsDisplayCustomItem || flightOrderInit.IsDisplayPurpose || flightOrderInit.IsDisplayAuthorizationCode || Number(flightOrder.TravelType) === 0 || flightOrder.ViolationCabinRankReason">
          <hs-cell-small-title v-if="flightOrderInit.IsDisplayCustomItem && flightOrder.CustomItem && flightOrder.CustomItem.Name"
            :title="flightOrderInit.CustomItemName"
            type="bold"
            :value="flightOrder.CustomItem.Name"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="flightOrderInit.IsDisplayPurpose"
            :title="$t('base.TravelPurpose')"
            type="bold"
            :value="flightOrder.Purpose"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="flightOrderInit.IsDisplayAuthorizationCode"
            :title="$t('base.AuthorizationCode')"
            type="bold"
            :value="flightOrder.AuthorizationCode"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="Number(flightOrder.TravelType) === 0" @click.native="showCExamineProcess = true"
            :class="{disabled: noValue(JSON.stringify(flightOrder.VettingProcessList))}"
            :title="$t('base.ApprovalInformation')"
            type="bold"
            :value="noValue(JSON.stringify(flightOrder.VettingProcessList)) ? $t('base.NoApprovalRequired') : (language === 'CN' ? '' : $t('base.ApprovalInformation'))"
            :showLogo="!noValue(JSON.stringify(flightOrder.VettingProcessList))">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="Number(flightOrder.TravelType) === 0"
            :title="$t('base.PaymentWay')"
            type="bold"
            :value="flightOrder.PayType | filterPayType"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="flightOrder.ViolationCabinRankReason"
            :title="$t('base.ViolationOfRank')"
            type="bold"
            :value="flightOrder.ViolationCabinRankReason"
            :warning="false"
            :showLogo="false"
            @header="_getTravelStandard">
          </hs-cell-small-title>
        </hs-section>

        <!-- 联系人 -->
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in flightOrder.Contacts" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.ContactInformation')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === flightOrder.Contacts.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>

    <!-- footer -->
    <div class="footer" @click="_switchDetailsDialog">
      <div class="price">
        <p>{{$t('base.TotalAmount')}}</p>
        <p>￥{{flightOrder.TotalPrice}}</p>
      </div>
      <div class="details">
        <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
    </div>

    <!-- 价格明细 -->
    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" :data="[flightOrder]" :isListenScroll="true" @scrollStart="_scrollStart" @click.native.stop>
        <ul>
          <hs-section :title="`${flightOrderInit.BackChargeInfo ? $t('base.Going') : ''}`" v-if="flightOrderInit.GoChargeInfo">
            <hs-cell-1 :showLogo="false" v-if="flightOrderInit.GoChargeInfo.SalePrice >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.AirTickets')}}</p>
                <p><span>￥{{flightOrderInit.GoChargeInfo.SalePrice}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="flightOrderInit.GoChargeInfo.FuelOilTax >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.FuelFee')}}</p>
                <p><span>￥{{flightOrderInit.GoChargeInfo.FuelOilTax}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="flightOrderInit.GoChargeInfo.AirportTax >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.PlaneConstructionFee')}}</p>
                <p><span>￥{{flightOrderInit.GoChargeInfo.AirportTax}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-for="(item, index) in flightOrderInit.InsuranceProducts" :key="index" v-if="item.Order === 1 && item.select">
              <div class="detailsDialog-custom-cell">
                <p>{{item.ClientShowName}}<template v-if="item.Order && !noValue(flightOrder.BackSelectedFlight.FlightNo)">&nbsp;|&nbsp;{{item.Order === 1 ? $t('base.Going') : $t('base.Return')}}</template></p>
                <p><span>￥{{item.SalePrice}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.Copy')}}</span></p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section :title="$t('base.Return')" v-if="flightOrderInit.BackChargeInfo">
            <hs-cell-1 :showLogo="false" v-if="flightOrderInit.BackChargeInfo.SalePrice >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.AirTickets')}}</p>
                <p><span>￥{{flightOrderInit.BackChargeInfo.SalePrice}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="flightOrderInit.BackChargeInfo.FuelOilTax >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.FuelFee')}}</p>
                <p><span>￥{{flightOrderInit.BackChargeInfo.FuelOilTax}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="flightOrderInit.BackChargeInfo.AirportTax >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.PlaneConstructionFee')}}</p>
                <p><span>￥{{flightOrderInit.BackChargeInfo.AirportTax}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="flightOrderInit.GoChargeInfo.Rebate > 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.FrontReturn')}}</p>
                <p><span>-￥{{flightOrderInit.GoChargeInfo.Rebate}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-for="(item, index) in flightOrderInit.InsuranceProducts" :key="index" v-if="item.Order === 2 && item.select">
              <div class="detailsDialog-custom-cell">
                <p>{{item.ClientShowName}}<template v-if="item.Order && !noValue(flightOrder.BackSelectedFlight.FlightNo)">&nbsp;|&nbsp;{{item.Order === 1 ? $t('base.Going') : $t('base.Return')}}</template></p>
                <p><span>￥{{item.SalePrice}}</span><span>x {{flightOrder.Passengers.length}}{{$t('base.Copy')}}</span></p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section v-if="flightOrderInit.GoChargeInfo">
            <hs-cell-1 :showLogo="false">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.ServiceFee')}}</p>
                <p><span>￥{{flightOrderInit.GoChargeInfo.ServiceCharge + (flightOrderInit.BackChargeInfo && flightOrderInit.BackChargeInfo.ServiceCharge ? flightOrderInit.BackChargeInfo.ServiceCharge : 0)}}</span><span>x {{flightOrder.Passengers.length}}人</span></p>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
      <div class="show-more" ref="show-more">{{$t('base.SlideUpToSeeMore')}}</div>
    </hs-dialog>
    <c-return-explain v-model="showExplain" :details="returnExplainDetails"></c-return-explain>
    <c-examine-process v-model="showCExamineProcess" :details="flightOrder" :passengerWarning="$t('base.Passenger')"></c-examine-process>
    <c-travel-standard v-model="showTravelStandard" :details="flightRight.FlightRank" :travelPolicy="flightRight.TravelPolicy"></c-travel-standard>

    <c-order-success ref="COrderSuccess" :XOrder="flightOrder" :OrderBusinessType="1"></c-order-success>
    <pay ref="Pay"></pay>
  </div>
</template>
<script type="text/ecmascript-6">
// 机票订单详情
import CExamineProcess from 'components/c-examine-process/c-examine-process-confirm'
import COrderSuccess from 'components/c-order-success/c-order-success.vue'
import CHeader from 'components/c-header/c-header'
import CReturnExplain from 'components/c-return-explain/c-return-explain'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsButton from 'base/hs-button/hs-button'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import Vue from 'vue'
import Pay from 'pages/pay/pay'
import {kpiFlightGetFlightCabinRule, kpiFlightConfirmOrder} from 'api'
import {mapGetters, mapMutations, mapActions} from 'vuex'
export default {
  created() {
    if (this.noValue(this.flightOrder.TravelType)) {
      this.$router.replace('/home')
    }
  },
  data() {
    return {
      showDetailsDialog: false,
      showExplain: false,
      returnExplainDetails: {},
      showCExamineProcess: false,
      submitSuccess: false,
      showTravelStandard: false
    }
  },
  computed: {
    ...mapGetters([
      'flightOrder',
      'flightOrderInit',
      'isMiniprogram',
      'flightRight'
    ]),
    // 是否为改期订单
    IsChangeOrder() {
      return !this.noValue(this.flightOrder.OriginPnrID)
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  methods: {
    ...mapMutations([
      'SET_FLIGHT_ORDER',
      'SET_FLIGHT_ORDER_INIT'
    ]),
    ...mapActions([
      'dispatchFlightRight'
    ]),
    // 退改签信息
    _getFlightCabinRule(flight, cabin) {
      let params = {
        DepartCode: flight.DepartCityCode,
        ArrivalCode: flight.ArrivalCityCode,
        FlightNo: flight.FlightNo,
        CabinID: cabin.CabinID,
        DepartDate: flight.DepartDateTime,
        IsChangeOrder: this.IsChangeOrder
      }
      console.log('退改签params： ', params)
      kpiFlightGetFlightCabinRule(params).then((res) => {
        console.log('退改签政策： ', res)
        this.showExplain = true
        this.returnExplainDetails = res.ResultData
      })
    },
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        this.showDetailsDialog = true
        setTimeout(() => {
          let dom = this.$refs.detailsDialog.$children[0].scroll
          if (dom.wrapperHeight < dom.scrollerHeight) {
            this.$refs['show-more'].style.opacity = 1
          } else {
            this.$refs['show-more'].style.opacity = 0
          }
        }, 400)
      }
    },
    /** 提交 */
    _confirm() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitYourOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            TravelID: this.flightOrder.TravelID
          }
          kpiFlightConfirmOrder(params).then((res) => {
            console.log('确认出票: ', res)
            if (res.ResultData === true) {
              this.submitSuccess = true
              /** 提交成功后 */
              this.$refs.COrderSuccess.open()
              // this.$alert({
              //   content: Number(this.flightOrder.PayType) === 2 ? this.$t('TM.TheCurrentOrderIsAPersonalPaymentOrder') : this.$t('TM.OrderSubmissionSuccessful'),
              //   confirmTxt: this.$t('base.CheckOrder'),
              //   cancelTxt: this.$t('base.ContinueBooking'),
              //   confirmFn: () => {
              //     this.$router.replace({name: 'planeTicketOrderList', params: {TravelType: Number(this.flightOrder.TravelType)}})
              //   },
              //   cancelFn: () => {
              //     this.$router.replace({path: '/planeTicket/query', query: {TravelType: this.flightOrder.TravelType}})
              //   }
              // })
              /********************************************************************************************************************/
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    /** 提交并支付 */
    _gotoSubmitAndPay() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitAndPayForTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            TravelID: this.flightOrder.TravelID
          }
          kpiFlightConfirmOrder(params).then((res) => {
            console.log('提交并支付: ', res)
            if (res.ResultData === true) {
              this.submitSuccess = true
              this._gotoPay()
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    /** 支付 */
    _gotoPay() {
      this.$refs.Pay && this.$refs.Pay.gotoPay({
        OrderBusinessType: 1,
        OrderID: this.flightOrder.TravelID,
        PayWay: 3,
        TravelType: Number(this.flightOrder.TravelType),
        CallbackPathName: 'planeTicketOrderList'
      })
    },
    _scrollStart() {
      this.$refs['show-more'].style.opacity = 0
    },
    _gotoHomePage() {
      this.$router.replace('/home')
    },
    // 获取差旅职级
    _getTravelStandard() {
      this.dispatchFlightRight().then((res) => {
        this.showTravelStandard = true
      })
    },
    // 展示/隐藏最低价详情
    _toggleOpenLowestPrice(key) {
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      res[key][0].openLowestPrice = !res[key][0].openLowestPrice
      this.SET_FLIGHT_ORDER(res)
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.showCExamineProcess === true) {
      this.showCExamineProcess = false
      next(false)
    } else {
      next()
    }
  },
  components: {
    CExamineProcess,
    COrderSuccess,
    CHeader,
    CReturnExplain,
    CTravelStandard,
    HsButton,
    HsCell1,
    HsCellSmallTitle,
    HsDialog,
    HsScroll,
    HsSection,
    Pay
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden
    .state-details
      width 100%
      padding 15px 0
      background-color $color_white
      >.header
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-bottom 8px
        font-size 1.05rem
        // >p
        //   &:last-child
        //     visibility hidden
      >.infor
        width 100%
        font-size 0.8rem
        color $color_gray_text
        line-height 1.4em
      >.buttons
        @extend .flex-row
        width 100%
        >.button
          flex 1
          margin 10px 5px 0 5px
          &:first-child
            margin-left 0px
          &:last-child
            margin-right 0px
    .travel-details
      width 100%
      padding-top 15px
      >.time
        @extend .flex-row
        @extend .flex-start
        width 100%
        margin-bottom 5px
        >.mark
          @extend .flex-center
          font-size 0.7rem
          border-radius 2px
          border 1px solid $color_theme
          color $color_theme
          padding 3px 0
          width 40px
          margin-right 10px
        >.mark[type="EN"]
          @extend .no-wrap
          width auto
          padding 3px
        >p
          width calc(100% - 50px)
          line-height 1.3em
      >.plane
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        width 100%
        font-size 0.8rem
        color $color_gray_text
        >.logo
          width 15px
          height 15px
          background-size 100% 100%
          margin-right 3px
        >.details
          width calc(100% - 13px)
          >p
            width 100%
            line-height 1.2em
            margin-bottom 2px
            >span
              color $color_theme
      >.details
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 15px 0
        >.source
        >.destination
          @extend .flex-column
          @extend .flex-center
          width 100px
          >p
            text-align center
            &:nth-child(1)
              font-size 1.5rem
              line-height 1.2em
              font-weight bold
              position relative
              >span
                color $color_theme
                font-size 0.7rem
                position absolute
                top -1rem
                right 0px
            &:nth-child(2)
              font-size 0.8rem
              line-height 1.4em
        >.to
          @extend .flex-center
          width calc(100% - 200px)
          position relative
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            background-image url('../../../../../static/image/home/trip_plane.png')
            background-color $color_white
          >.dashed
            width 100%
            height 0px
            position absolute
            left 0
            top 50%
            border-top 1px dashed $color_black_text_0
          >.stopOver
            position absolute
            top 18px
            left 0
            font-size 0.6rem
            color $color_theme
            width 100%
            text-align center
          >.stopOver-logo
            @extend .flex-center
            width 24px
            height 18px
            background-color $color_white
            z-index 9
            >p
              text-align center
              width 18px
              line-height 1.2em
              color $color_theme
              font-size 0.6rem
    .min-price-details-header
      @extend .flex-row
      @extend .flex-between
      width 100%
      >p
        font-size 1rem
        color $color_black_text_1
      >div
        width 20px
        height 20px
        background-size 100% 100%
        transition all 400ms
    .min-price-details
      width 100%
      background-color $color_gray_background_0
      padding 10px 15px 0 15px
      >div
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 5px 0
        >p
          font-size 1rem
          color $color_black_text_1
          line-height 1.2em
          &:nth-child(1)
            margin-right 10px
          &:nth-child(2)
            color $color_black_text_0
            font-weight bold
            max-width 100%
            word-break break-all
        &:last-child
          flex-wrap wrap
          >p
            &:nth-child(1)
              width 170px
              margin-bottom 3px
        >.price
          color $color_theme !important
    .cost-details
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 15px 0
      font-size 0.8rem
      .details
        @extend .flex-column
        @extend .flex-top
        >div
          margin 2px 0
          >span
            &:nth-child(1)
              margin-right 6px
            &:nth-child(2)
              color $color_theme
      >p
        font-size 0.8rem
        color $color_gray_text
    .cost-details[type="EN"]
      font-size 0.7rem
    .insure-details
      font-size 0.9rem
      min-height 44px
      padding 10px 0
      >p
        line-height 1.5em
    // .custom-contact
    //   @extend .flex-column
    //   @extend .flex-top
    //   font-size 0.9rem
    //   line-height 1.4em
    //   padding 15px 0
    .custom-cell-people
      @extend .flex-between
      @extend .flex-start
      @extend .flex-top
      width 100%
      >.left
        width 90px
        padding 15px 0
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          color $color_black_text_1
          line-height 1.4em
      >.right
        width calc(100% - 100px)
        padding 15px 0
        border-bottom 1px dashed $color_gray_layer
        >.name
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          font-weight bold
          line-height 1.4em
        >.others
          width 100%
          >p
            line-height 1.5em
            color $color_gray_text
            font-size 1rem
            &:first-child
              margin-top 7px
          >.insure
            font-size 0.8rem
    .cell-3-value
      font-weight bold
    .cell-3-title
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-left 3px
  >.footer
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
    >.price
      @extend .flex-row
      @extend .flex-start
      width calc(100% - 30px)
      height 100%
      >p
        font-size 1rem
        &:nth-child(2)
          color $color_theme
          margin-left 5px
    >.details
      @extend .flex-row
      @extend .flex-between
      width 20px
      height 100%
      >p
        font-size 0.9rem
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms
  .show-more
    @extend .flex-center
    position absolute !important
    width 100%
    left 0
    bottom 0
    height 15px
    color $color_black_text_0
    font-size 0.6rem
    transition all 300ms
  .detailsDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    .detailsDialog-custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 44px
      >p
        font-size 0.8rem
        &:nth-child(1)
          color $color_black_text_0
        &:nth-child(2)
          color $color_theme
          >span
            &:nth-child(2)
              color $color_black_text_0
              margin-left 5px
</style>



